.app {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgb(255, 0, 0);
  color: rgb(255, 255, 255);
}

::-moz-selection {
  background: rgb(255, 255, 255);
  color: #000000;
  opacity: 1;
}

::selection {
  background: rgb(255, 255, 255);
  color: #000000;
  opacity: 1;
}

::-moz-selection {
  background: rgb(255, 255, 255);
  color: #000000;
}

p {
  font-family: "Sneak", Arial, sans-serif;
  font-size: 6rem;
  line-height: 8rem;
  letter-spacing: -0.05rem;
  position: relative;
  z-index: 999;
  word-break: break-word;
}

@-webkit-keyframes blinker {
  50% { opacity: 0; }
}

@keyframes blinker {
  50% { opacity: 0; }
}

@-webkit-keyframes jump {
  50% { top: -1rem; }
}

@keyframes jump {
  50% { top: -1rem; }
}

@-webkit-keyframes hello {
  50% { color: transparent; -webkit-text-stroke-width: 1px; -webkit-text-stroke-color: rgba(255, 255, 255, 1); }
}

@keyframes hello {
  50% { color: transparent; -webkit-text-stroke-width: 1px; -webkit-text-stroke-color: rgba(255, 255, 255, 1); }
}

.super {
  vertical-align: super;
  font-size: 3rem;
  margin-left: 1rem;
}

span.indent {
  display: inline-block;
  text-indent: 8rem;
}

p a {
  text-decoration: underline;
  color: rgb(255, 255, 255);
  /* border-bottom: 0.125rem solid rgb(255, 255, 255); */
  line-height: 4.8rem;
  /* white-space: nowrap; */
  position: relative;
  /* display: inline-block; */
}

p a.internal {
  /*-webkit-text-stroke-color: rgba(255, 255, 255, 1);
  -webkit-text-stroke-width: 1px;
  animation: hello 0.2s steps(1) 5; */
}

p a.internal.johanne {
  /*animation: jump 0.5s steps(1) 5;*/
}

p a.internal:hover {
  color: rgba(255, 255, 255, 1);
  -webkit-text-stroke-width: 0px;
  cursor: help;
}

p a.external {

}

p a.external:hover {
  -webkit-animation: blinker 0.2s steps(3) infinite;
          animation: blinker 0.2s steps(3) infinite;
}

span.number {
  background: transparent;
  border: 0.125rem solid rgba(255, 255, 255, 1);
  width: 3rem;
  height: 3rem;
  display: inline-block;
  text-align: center;
  top: -2rem;
  margin-left: .5rem;
  position: relative;
  color: rgba(255, 255, 255, 1);
  font-size: 2rem;
  border-radius: 50%;
  line-height: 3rem;
  text-indent: 0rem !important;
  text-align: left;
}

span.number > span {
  position: absolute;
  width: 3rem;
  height: 3rem;
  text-align: center;
}

a.johanne:hover {
  color: rgb(255, 0, 0) !important;
  border-color: rgb(255, 0, 0);
}

a.artdirector:hover {
