  overflow-y: scroll;
  padding-bottom: 20rem;
}

.content-inner {
  width: 80%;
  max-width: 110rem;
  margin: auto;
  padding-top: 10rem;
  padding-bottom: 10rem;
}

@media (max-width: 600px) {
